body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.navbar-banana{
  background-color: #fff9e5;
  padding: 0;
}

.navbar-logo{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
}
.navbar-expand-md .navbar-collapse {
  margin-left: 1%;
}
.subtitle{
  font-size: 0.7em;
}
.nav-item {
  padding-right: 0.7rem;
  padding-left: 0.7rem;
}


.contact{
  display: flex;
}
/*******Mobile********/
@media only screen and (max-width: 1099px) {
  .contact{
    flex-direction: column;
  }

  .contactDetails{
    background-color: #2c3345;
    flex-direction: column;
  }

  .night{
    display: none;
  }
  
  .detail{
    margin-top: 10%;
  }

  .contactForm, .contactDetails{
    width: 100%;
  }
}

/*******DESKTOP********/
@media screen and (min-width: 1100px) {
  .contact{
    flex-direction: row;
  }

  .contactDetails{
    background-color: #2c3345;
    flex-direction: row-reverse;
  }
  .detail{
    display: block;
    position: absolute;
    z-index: 1;
    margin-left: 2%; 
  }
  
  .detail1{
    padding-top: 7%; 
  }
  
  .detail2{
    padding-top: 24%; 
  }

  .contactDetails{
    width: 100%;
  }

  .contactForm{
    width: 100%;
  }

}


.contactForm{
  background-color: #B2D9D3;  
  min-height: 90vh;
  display: flex;
  flex-direction: column;
}

.contactDetails{
  min-height: 90vh;
  display: flex;
}
.details{
  display: flex;
  flex-direction: column;
  color: white;
  text-align: left;
  padding-left: 10%;
}

.night{
  height: 90vh;
}

.day{
  right: 0;
}

.form{
  width: 60%;
  margin: auto;
  text-align: left;
}
.App {
  text-align: center;
}

*{
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header{
  background-color: #fff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: grey;
}

.page{
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  flex-direction: column;
  width: 80%;
  margin: auto;
}

.home{
  flex-direction: column;
  min-height: 100vh;
  width: 80%;
  margin: auto;
}

.homeLogo{
  margin-top: 50%;
}

.map-logo{
  margin: 40px;
}


.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.places{
  flex-direction: row;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #E3A357;
}
.carusel{
  background-color: #E3A357;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.carusel-title{
  font-weight: bold;
  font-size: 1.5em;
  padding-top: 5%;
}

.carousel, .slide{
  width: 90%;
  margin-bottom: 5%;
}
.place{
  margin-left: 2%;
  margin-right: 2%;
}
.place-title{
  font-size: 1.2em;
  font-weight: bold;
}
.place-subtitle{
  font-size: 0.6em;
}
.small-p{
  font-size: 0.6em;
}

.about{
  height: 100vh;
}

.title-is-2{
  font-size: 1.2em;
}

.subtitle-is-2{
  font-size: 0.8em;  
}


